@import "./src/styles/setup";
.sign-up {
  .reject-modal {
        position: fixed;
        width: 360px;
        height: 100px;
        background-color: #d9d9d9;
        z-index: 20;
        top: 20%;
        left: 50%;
        transform: translate(-50%, -50%);
        box-shadow: 0 0 7px #202020d9;
        border: 1px solid #545454;
        border-radius: 5px;
        padding: 12px 25px;
        color: #0b0b0bd3;

    p {
        font-family: $font-primary;
        text-align: center;
    }

    .cls-btn {
        background-color: transparent;
        color: #1e1e1e;
        padding: 3px 12px;
        border: 2px solid gray;
        border-radius: 5px;
        font-weight: bold;
        margin-top: rem(10px);
    }
  }
}
